import React from 'react';
import { Radio, Statistic, Row, Col, Divider, Table, Space, Button, Tooltip, Typography } from 'antd';
import { EditOutlined, DeleteOutlined } from '@ant-design/icons';

import Breadcrumbs from '../Common/Breadcrumbs';
import { cardlayout } from '../../constants/layout'
import Checkbox from 'antd/lib/checkbox/Checkbox';

const { Title } = Typography;

const index = () => {
    const path = [
        'Orders',
        'Manage'
    ]

    const columns = [
        {
            title: 'Name',
            dataIndex: 'name',
            key: 'name',
            render: text => <a>{text}</a>,
            sorter: true,
        },
        {
            title: 'Document',
            dataIndex: 'document',
            key: 'document',
            sorter: true,
        },
        {
            title: 'Order Date',
            dataIndex: 'orderDate',
            key: 'orderDate',
            sorter: true,
        }, {
            title: 'Pending Since',
            dataIndex: 'pending',
            key: 'pending',
            sorter: true,
        }, {
            title: 'Sale Price',
            dataIndex: 'sale',
            key: 'sale',
            sorter: true,
        }, {
            title: 'Payment Method',
            dataIndex: 'paymentMethod',
            key: 'paymentMethod',
            sorter: true,
        }, {
            title: 'Available',
            dataIndex: 'available',
            key: 'available',
        }, {
            title: 'Retail Price',
            dataIndex: 'retailPrice',
            key: 'retailPrice',
        }, {
            title: 'Quantity',
            dataIndex: 'quantity',
            key: 'quantity',
        }, {
            title: 'Status',
            dataIndex: 'status',
            key: 'status',
        }, {
            title: 'Ship on time SLA',
            dataIndex: 'ship',
            key: 'ship',
        }, {
            title: 'Printed',
            dataIndex: 'printed',
            key: 'printed',
            render: () => {
                <Checkbox />
            }
        }, {
            title: 'Action',
            dataIndex: 'action',
            key: 'action',
            render: (text, record) => (
                <Space size="small">
                    <Tooltip title="Edit">
                        <Button type="success" icon={<EditOutlined />} />
                    </Tooltip>
                    <Tooltip title="Delete">
                        <Button type="danger" icon={<DeleteOutlined />} />
                    </Tooltip>
                </Space>
            ),
        }
    ];

    const tabledata = [
        {
            key: '1',
            name: 'John Brown',
            document: 'Invoice',
            orderNo: '002120023',
            orderDate: '2020-10-02',
            pending: '4 days',
            paymentMethod: 'COD',
            retailPrice: 1231,
            quantity: 5,
            status: 'Ready To Ship',
            ship: '2020-10-02',
            printed: true,
        },
    ];
    return (
        <div>
            <Breadcrumbs crumb={path} />
            <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
                <Row gutter={16} className='product-stat'>
                    <Col {...cardlayout}>
                        <Statistic title="All" value={1128} />
                    </Col>
                    <Col {...cardlayout}>
                        <Statistic title="Pending" value={93} />
                    </Col>
                    <Col {...cardlayout}>
                        <Statistic title="Ready To Ship" value={45} />
                    </Col>
                    <Col {...cardlayout}>
                        <Statistic title="Shipped" value={143} />
                    </Col>
                    <Col {...cardlayout}>
                        <Statistic title="Completed" value={433} />
                    </Col>
                </Row>
                <Divider />
                <Radio.Group size='large'>
                    <Radio.Button value="Add New">Order Number</Radio.Button>
                    <Radio.Button value="Export">Customer</Radio.Button>
                    <Radio.Button value="Shop Category">Product</Radio.Button>
                    <Radio.Button value="Import">Product Tag</Radio.Button>
                    <Radio.Button value="View History">Seller SKU</Radio.Button>
                    <Radio.Button value="Name">Payment</Radio.Button>
                    <Radio.Button value="Seller SKU">Delivery Option</Radio.Button>
                    <Radio.Button value="Shop SKU">Date</Radio.Button>
                    <Radio.Button value="Brand Name">Add Product SKU Code</Radio.Button>
                </Radio.Group>
                <Divider />
                <Title level={3}>Products</Title>
                <Table columns={columns} dataSource={tabledata} />
            </div>
        </div >
    );
};

export default index;